const {ccclass, property} = cc._decorator;

@ccclass
export default class KejaruController extends NetaJi {

    @property
    lifePoints:number = 100

    takeHit(evt) {
        this.lifePoints -= evt.points;
        
        this.node.emit("play-animation", {name:"takingHit"});

        if(this.lifePoints<=0) {
            this.node.emit("play-animation", {name:"die"});
        }
    }

    start () {
        this.node.on("takeHit", this.takeHit, this);
    }



    // update (dt) {}
}
