const {ccclass, property} = cc._decorator;

@ccclass
export default class StarRepositoryController extends cc.Component {


    // LIFE-CYCLE CALLBACKS:
    @property
    suns:number = 0;


    @property(cc.Label)
    pointsLabel:cc.Label = null;

    collectSuns(evt) {
        this.suns += evt.points;
        this.pointsLabel.string = this.suns.toString();
        this.node.emit("suns-changed",{suns:this.suns});
    }

    onLoad () {
        this.node.on("update-suns", this.collectSuns, this);
        this.pointsLabel.string = this.suns.toString();
    }

    start () {

    }

    // update (dt) {}
}
