const {ccclass, property} = cc._decorator;

@ccclass
export default class HealthController extends cc.Component {

    @property
    max:number= 10
    @property
    hitStep:number = 1;
    
    currentHealth:number = 0
    progressBar:cc.ProgressBar = null;

    handleTakingHit() {
        this.currentHealth = this.currentHealth - this.hitStep;

        if(this.currentHealth>0) {
            this.progressBar.progress = this.currentHealth/this.max;
        } else {
            this.progressBar.progress = 0
            this.node.emit("die");
        }
    }

    updateHealth(evt) {
        this.currentHealth = this.currentHealth - evt.points;

        if(this.currentHealth>0) {
            this.progressBar.progress = this.currentHealth/this.max;
        } else {
            this.progressBar.progress = 0
        }
    }
    
    onLoad () {
        this.progressBar = this.node.getComponent(cc.ProgressBar);
        this.currentHealth = this.max;

        this.node.on("takeHit", this.updateHealth, this);
    }
}
