const {ccclass, property} = cc._decorator;

@ccclass
export default class MovementController extends cc.Component {

    @property
    walkingSpeed:number = 10

    @property
    left:boolean = false

    canWalk:boolean = true;

    setCanWalk(canWalk:boolean) {
        this.canWalk = canWalk;
    }

    start() {
        // this.scheduleOnce(function() {
        //     this.node.destroy();
        // }.bind(this), 25);


        this.scheduleOnce(this.checkBounds, 2);
    }


    checkBounds() {
        if(this.node.x + this.node.width<-100 || this.node.x+this.node.width> cc.winSize.width +100 ||
            this.node.y + this.node.height<-100 || this.node.y+this.node.height > cc.winSize.height + 100            
            ) {
            this.node.destroy();
        }
        
    }

    update (dt) {
        this.checkBounds();
        if(!this.canWalk)
            return;
        if(this.left){
            this.node.x = this.node.x - this.walkingSpeed*dt;
        }
        else {
            this.node.x = this.node.x + this.walkingSpeed*dt;
        }
    }
}
