const {ccclass, property} = cc._decorator;

@ccclass
export default class ButtonClickHandler extends cc.Component {

    @property
    scaleTo:number=1.1;
    @property
    totalAnimationTime:number=.4;

    bubbleEvent(){
        cc.find("/Canvas").emit("handleClick",{
            name:this.node.name
        });

        this.loadScene();
    }
    loadScene(){

    }

    handleClick() {
        this.node.runAction(cc.sequence([cc.scaleTo(this.totalAnimationTime/2, this.scaleTo, this.scaleTo), cc.scaleTo(this.totalAnimationTime/2, 1,1), cc.callFunc(this.bubbleEvent, this)]));
        
    }

    onLoad(){
        this.node.on("touchend", this.handleClick, this);
    }

    start () {
        
    }

    onDestroy () {
        this.node.off("touchend", this.handleClick, this);
    }
}
