import TileClickHandler from "../groundTile/TileClickHandler";
import LawnMover from "../../gameObjects/lawnMover/LawnMover";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GameLayoutHandler extends cc.Component {

    @property
    rows: number  = 5

    @property(cc.Prefab)
    groundTile:cc.Prefab = null;

    @property(cc.Node)
    garden:cc.Node = null

    @property
    columns:number = 9;

    tileScale:cc.Vec2;

    tileNode:cc.Node;

    scene:cc.Scene;

    expectedWith:number = 1;
    expectedHeight:number = 1;

    @property(cc.Prefab)
    lawnMover: cc.Prefab = null;

    calculateScale() {
        let width = this.node.width;
        let height = this.node.height;
        this.expectedWith = width/this.columns;
        this.expectedHeight = height/this.rows;
    }

    getTilePosition(row:number, col:number) {
        let x = col*this.expectedWith + this.expectedWith/2;
        let y = row*this.expectedHeight+ this.expectedHeight/2;
        return new cc.Vec2(x, y);
    }

    tiles={}
    onLoad() {
        this.calculateScale();
        this.scene = cc.director.getScene();
        cc.director.getCollisionManager().enabled = true;
        let index = 0;
        for(let row=0;row<this.rows;row++) {
            for(let col=0;col<this.columns;col++) {
                let Tile = cc.instantiate(this.groundTile);
                let tileClickHandler:TileClickHandler = Tile.getComponent(TileClickHandler);
                let position = this.getTilePosition(row, col);
                tileClickHandler.setMetaData(row, col, index, position);
                Tile.width = this.expectedWith;
                Tile.height = this.expectedHeight;
                this.garden.addChild(Tile);
                // console.log("row column pair are ", Tile.getPosition());
                Tile.setPosition(position);
                this.tiles[index] = tileClickHandler;
                this.placeLawnMover(index);
                index++;
            }
        }

        this.node.emit("tilesLayed",{
            tiles: this.tiles
        });
    }


    onCollisionEnter(other:cc.Collider, self:cc.Collider) {
        if(other.node.group === "netaJi" && self.tag == 100) {
           cc.find("/Canvas").emit("NetaJiWins");
        }
    }

    placeLawnMover(index) {
        if(index%this.columns == 0) {
            let lawanMover = cc.instantiate(this.lawnMover);
            this.garden.addChild(lawanMover);
            let lawnPos = this.tiles[index].worldPos;

            lawanMover.setPosition(new cc.Vec2(lawnPos.x- lawanMover.width*lawanMover.scaleX, lawnPos.y));

        }
    }



    update (dt) {}
}
