const {ccclass, property} = cc._decorator;

@ccclass
export default class IntroductionScript extends cc.Component {

    play:cc.Node = null
    about:cc.Node = null
    soundOn:cc.Node = null

    onLoad () {
        this.play = cc.find("/Canvas/play");
        this.about = cc.find("/Canvas/about");
        this.soundOn = cc.find("/Canvas/sound");
        cc.find("/Canvas").on("handleClick", this.handleClick, this);
    }

    handleClick(evt) {
        if(evt.name === "play") {
            cc.director.loadScene("world1LevelSelector");
        }
    }




}
