const {ccclass, property} = cc._decorator;

@ccclass
export default class ProgressLoader {

    savedProgress: GameProgress = null

    public loadGame() {
        return JSON.parse(cc.sys.localStorage.getItem('gameProgress')) as GameProgress;
    }


    public saveGame(userProgress:GameProgress) {
        cc.sys.localStorage.setItem('gameProgress', JSON.stringify(userProgress));
    }
    

    start () {
        this.loadGame();
    }



    // update (dt) {}
}
