const {ccclass, property} = cc._decorator;

@ccclass
export default class KitanuController extends cc.Component {

    @property
    speedX:number = 30

    @property
    speedY:number = 10

    @property
    hitPoints:number = 10

    @property
    yDisplacement=10

    @property
    timeToLive:number = 20

    kitanuCanHit:Array<string> = ["EGG", "PLANT"];

    currentTime=0


    onCollisionEnter(other:cc.Collider, self:cc.Collider) {
        if(this.kitanuCanHit.indexOf(other.node.group)>-1 ) {
            other.node.emit("takeHit", {points: this.hitPoints})
            this.node.destroy();
        }
    }


    start () {
        cc.director.getCollisionManager().enabled = true;
    }

    getXCordinate(dt) {
        return this.speedX*dt;
    }

    angle = 0
    update (dt) {

        this.currentTime += dt;
        if(this.currentTime>this.timeToLive) {
            this.node.destroy();
        }
        this.angle += 360*dt;
        this.node.x = this.node.x - this.getXCordinate(dt);
        this.node.y = this.node.y+ this.speedY*dt + this.yDisplacement*Math.cos(this.angle);
    }
}
