//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class PeaShooter extends Plant {

    @property(cc.Prefab)
    pea:cc.Prefab = null


    @property
    frequencyOfFiringPerSecond:number = 1;

    proximityTagForNetaJi:number = 10;
    firingPosition:cc.Node = null;

    @property
    health:number = 200

    shouldShoot:boolean = false;

    currentTime: number = 0;


    takeHit(evt) {
        this.health -= evt.points;
        this.healthIndicator.emit("takeHit", evt);
        if(this.health<0) {
            this.unschedule(this.fire);
            this.node.destroy();
            evt.node.emit("doneEating");
        }
    }

    reactToNetajiProximity(isNetaJiNear) {
        this.shouldShoot = isNetaJiNear;
    }


    start () {
        this.node.on("takeHit", this.takeHit, this);
        this.firingPosition = this.node.getChildByName("firingPosition");
    }

    fire() {
        let pea:cc.Node = cc.instantiate(this.pea);
        let scene = cc.director.getScene();
        pea.setScale(this.node.getScale(new cc.Vec2()));
        scene.addChild(pea);
        pea.setPosition(this.node.convertToWorldSpaceAR(this.firingPosition.getPosition()));
        let audioSource:cc.AudioSource = this.node.getComponent(cc.AudioSource);
        audioSource.play();
    }

    update (dt) {
        this.currentTime += dt;
        if(this.shouldShoot && this.currentTime>this.frequencyOfFiringPerSecond) {
            this.currentTime = 0;
            this.fire();
        }
    }
}
