const {ccclass, property} = cc._decorator;

@ccclass
export default class StarsGenerator extends cc.Component {

    @property(cc.Prefab)
    star: cc.Prefab = null;

    @property(cc.Node) 
    ground:cc.Node = null

    @property
    minTimeToWait:number = 3


    timeToLive:number = 5;

    targetTime:number = 3;


    currentTime = 0;


    launchStar() {
        let width = this.ground.width;
        let height = this.ground.height;
        let starPoint :cc.Vec2= this.ground.convertToWorldSpaceAR(new cc.Vec2(Math.random()*width, Math.random()*height));
        let starNode = cc.instantiate(this.star);
        cc.director.getScene().addChild(starNode);
        starNode.setPosition(new cc.Vec2(200, cc.winSize.height));
        starNode.runAction(cc.moveTo(1, starPoint));
        this.targetTime = Math.random()*10 + this.minTimeToWait;
        //console.log("New target time is ", this.targetTime);
        this.currentTime = 0;
    }

    start () {
        
    }
    update (dt) {
        this.currentTime += dt;
        if(this.currentTime>=this.targetTime) {
            this.launchStar();
        }

        


    }
}
