const {ccclass, property} = cc._decorator;

@ccclass
export default class PlayAndResumeButtonHandler extends ButtonClickHandler {

    @property
    isPauseButton:boolean = false;

    @property(cc.Prefab)
    popupPrefab:cc.Prefab = null;

    @property
    insidePopUp:boolean = false;

    @property(cc.Node)
    rootNode:cc.Node = null

    onload() {
        super.onLoad();
    }
    
    loadScene() {

        if(this.insidePopUp) {
            this.rootNode.active = false;
        }

        if(this.isPauseButton) {
            cc.director.pause();
            if(!this.insidePopUp) {
                let popUp = cc.instantiate(this.popupPrefab);
                cc.director.getScene().addChild(popUp);
                popUp.getChildByName("layerBg").width = cc.winSize.width;
                popUp.getChildByName("layerBg").height = cc.winSize.height;
                popUp.setPosition(new cc.Vec2(cc.winSize.width/2, cc.winSize.height/2));
                cc.director.resume();
            }

        }else {
            cc.director.resume();
        }
    }

    // update (dt) {}
}
