import SpeechMovement from "../speech/SpeechBoubleMovement";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SpeechHandler extends cc.Component {

    @property(cc.Prefab)
    speechBuble:cc.Prefab = null;

    speechOrigin:cc.Node = null;

    speeches:string[] = [
        "Ye shadyantra hai!",
        "Modi ji ne kiya hai ye... ",
        "Hum free Wifi Denge",
        "Uhhh Hunnn .... "
    ];

    onLoad() {
        this.speechOrigin = this.node.getChildByName("speechOrigin");
    }

    deliverSpeech() {
        let randomSpeechIndex = Math.floor(Math.random()*(this.speeches.length-1)) ;
        let speechNode = cc.instantiate(this.speechBuble);
        let scene = cc.director.getScene();
        scene.addChild(speechNode);
        let label:SpeechMovement = speechNode.getComponent("SpeechBoubleMovement"); 
        label.setText(this.speeches[randomSpeechIndex]);
        speechNode.setPosition(this.node.convertToWorldSpaceAR(this.speechOrigin.getPosition()));
    }
}
