const {ccclass, property} = cc._decorator;

@ccclass
export default class YouWinPopUp extends cc.Component {

    @property
    currentScene:string  = "";

    @property
    nextScene:string  = "";

    @property
    stars:number = 2;

    @property
    winText:string = "";

    start () {
        this.node.getChildByName("StarCollector").getComponent(StarSelector).stars = this.stars;
        this.node.getChildByName("StarCollector").getChildByName("Score").getComponent(cc.Label).string = this.winText;
        this.node.getChildByName("restartButton").getComponent(LevelSelectorButton).sceneName = this.currentScene;
        this.node.getChildByName("playButton").getComponent(LevelSelectorButton).sceneName = this.nextScene;
    }

    // update (dt) {}
}
