const {ccclass, property} = cc._decorator;

@ccclass
export default class TileClickHandler extends cc.Component {

    @property(cc.SpriteFrame)
    clickedSprite: cc.Sprite = null;

    currentSprite:cc.Sprite;

    row:number=0;
    column:number=0;
    index: number = 0;
    width: number = 0;
    height: number =0;
    worldPos: cc.Vec2 = null;

    getBottomMostMitPoint(){
        return new cc.Vec2(this.worldPos.x, this.worldPos.y-this.height/2);
    }

    setMetaData(row, col, index, pos) {
        this.row = row;
        this.column = col;
        this.index = index;
        this.worldPos = pos;
        this.width = this.node.width;
        this.height = this.node.height;
    }

    getPosition() {
        return new cc.Vec2(this.row, this.column);
    }


    getWorldPosition() {
        return this.worldPos;
    }

    handleTileClikedDone() {
        console.log("The person has clicked row , col, index as ", this.row, this.column, this.index);

        // this.currentSprite.spriteFrame.name = this.clickedSprite;

        cc.find("/Canvas").emit("tileClicked", {
            row: this.row,
            column: this.column,
            index : this.index,
            pos: this.worldPos,
            width: this.node.width,
            height: this.node.height,
            lowerCenter: this.getBottomMostMitPoint()
        });
    }

    onLoad() {
        this.currentSprite = this.node.getComponent(cc.Sprite);
        // this.node.on(cc.Node.EventType.TOUCH_MOVE, this.handleTileClicked, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.handleTileClikedDone, this);
        // this.node.on(cc.Node.EventType.TOUCH_CANCEL, this.handleTileClikedDone, this);
    }

    onDestroy() {
        this.node.off(cc.Node.EventType.TOUCH_CANCEL, this.handleTileClikedDone, this);
    }

}
