const {ccclass, property} = cc._decorator;

@ccclass
export default class WifiController extends cc.Component {

    @property
    firingFrequency:number = 3

    @property
    health:number = 100

    

    @property(cc.Prefab)
    wavePrefab:cc.Prefab = null

    originPointOfWave:cc.Node = null;

    launchWave() {

        this.originPointOfWave = this.node.getChildByName("waveOrigin");
        let wave = cc.instantiate(this.wavePrefab);
        cc.director.getScene().addChild(wave);
        let worldPoint = this.node.convertToWorldSpaceAR(this.originPointOfWave.getPosition());
        wave.setPosition(worldPoint);
    }

    onLoad () {
        cc.director.getCollisionManager().enabled = true;
    }

    onCollisionEnter(other:cc.Collider, self:cc.Collider) {
        if(other.node.group === "bullet") {
            other.node.emit("takeHitFromNetaJi", {points: this.damage,
                damager: this.node.group})
            this.node.destroy();
        }
    }

    start () {
        this.schedule(this.launchWave,this.firingFrequency, 1000, 2);
    }

    // update (dt) {}
}
