const {ccclass, property} = cc._decorator;

@ccclass
export default class KejaruSideController extends NetaJi {

    @property
    totalHealth:number = 90

    @property
    offsetFromBotoom:number = 5

    @property
    timesKejaruCanDefend:number = 10


    @property
    canDefend:boolean = true

    @property
    damageFactorInDefence:number = .2

    @property
    damagePerSecond: number= 25


    skeleton:dragonBones.ArmatureDisplay
    currentAnimation:string = null

    animations:string[] = []

    @property
    walkingSpeed:number = 10

    @property
    left:boolean = false

    healthIndicator:cc.Node = null

    canWalk:boolean = true;

    walkingRow: number = 0;

    setCanWalk(canWalk:boolean) {
        this.canWalk = canWalk;
    }
    originalTimeScala:number = 1


    updateBounds() {
        if(this.node.getPosition().x < 10 ){
            this.node.emit('kejru-wins',{position:this.node.convertToWorldSpaceAR(this.node.getPosition())});
        }
        this.node.destroy();
    }

    playAnimation(name, playTimes) {

        if(this.skeleton.animationName == "eatPlant") {
            return;
        }


        if(name == "walk") {
            this.canWalk = true;
        }
        this.skeleton.playAnimation(name, playTimes);
    }



    takeHit(evt) {
        this.totalHealth -= evt.points;
        
        this.healthIndicator.emit("takeHit", {points: evt.points*this.damageFactorInDefence});
        if(this.totalHealth<=0) {
            cc.find("/Canvas").emit("netaJiDied", {uuid: this.node.uuid});
            this.node.destroy();
        }
        this.canWalk = false;
        this.skeleton.timeScale = this.originalTimeScala ;
        this.playAnimation("walk",1000);
    }

    currentPlantToEat:cc.Node = null

    eatCurrentPlant() {
        this.currentPlantToEat.emit("takeHit", {points: this.damagePerSecond,
            node: this.node});
    }

    eatPlant(evt) {
        this.canWalk = false;
        this.playAnimation("eatPlant", 100); 
        this.currentPlantToEat = evt.plant.node;
        this.schedule(this.eatCurrentPlant, 1, 1000,0);   
    }

    aboutToTakeHit() {
        if(this.skeleton.animationName == "eatPlant" || !this.canDefend) {
            return;
        }

        if(10*Math.random()<=3 && this.timesKejaruCanDefend>=1) {
            console.log("Only 30% of the hits will be defended");
            return;
        }

        this.timesKejaruCanDefend -= 1;
        if(this.timesKejaruCanDefend <0 ) {
            return;
        }
        this.canWalk = false;
        this.skeleton.timeScale = 3;
        this.playAnimation("defend", 1);
    }

    doneEating() {
        this.unschedule(this.eatCurrentPlant);
        this.canWalk = true;
        this.skeleton.playAnimation("walk", -1);
    }

    start () {

        super.start();

        this.node.on("takeHit", this.takeHit, this);
        this.node.on("aboutToTakeHit", this.aboutToTakeHit, this);
        this.node.on("eatPlant", this.eatPlant, this);
        this.node.on("doneEating", this.doneEating, this);
        this.originalTimeScala = this.skeleton.timeScale;
        

    }

    onLoad() {
        this.skeleton = this.node.getComponent(dragonBones.ArmatureDisplay);
        this.animations = this.skeleton.getAnimationNames(this.skeleton.armatureName);
        this.healthIndicator = this.node.getChildByName("healthIndicator");
        
    }

    
    

    
    update (dt) {
        super.update(dt);
        //this.updateBounds();

        if(!this.canWalk)
            return;
            
        if(this.left){
            this.node.x = this.node.x - this.walkingSpeed*dt;
        }
        else {
            this.node.x = this.node.x + this.walkingSpeed*dt;
        }
    }
}
