const {ccclass, property} = cc._decorator;

@ccclass
export default class StarTapHandler extends cc.Component {

    target:cc.Node = null;

    @property
    points:number = 25;

    targetPosition:cc.Vec2 = null;

    rotationSpeed:number=50;



    liveTime = 0;
    timeToLive:number = 5;

    // LIFE-CYCLE CALLBACKS:

    reachedTarget() {
        this.target.emit("update-suns",{points:this.points});
        this.node.destroy();
    }

    onDestroy() {
        this.node.off(cc.Node.EventType.TOUCH_END, this.handleClick, this);
    }

     onLoad () {
       
        //this.target = cc.find("/Canvas/HUD/SunIndicator");
        //this.targetPosition = this.target.convertToWorldSpaceAR(new cc.Vec2());
        
        this.node.on(cc.Node.EventType.TOUCH_END, this.handleClick, this);
     }

     handleClick() {
        this.node.runAction(cc.sequence(cc.moveTo(1, this.targetPosition ), cc.callFunc(this.reachedTarget, this)));
     }

    start () {

    }

    update (dt) {
        this.node.angle += this.rotationSpeed*dt;


        this.liveTime += dt;

        if(this.liveTime>this.timeToLive) {
            this.node.destroy();
        }
    }
}
