import StarRepositoryController from "./StarRepositoryController";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SelectPlantController extends cc.Component {


    @property
    refreshRate:number = 10;

    @property(cc.Prefab)
    plantToSeed:cc.Prefab = null;


    sunRepository:cc.Node = null;

    sunRepositoryController:StarRepositoryController = null;

    @property
    points:number = 1

    tileHeight:number = 0

    currentHeight:number = 0

    enoughPoints:boolean = false

    refreshing:boolean = false

    selected: boolean = false;      

    veil:cc.Node = null;

    selectedNode:cc.Node =null;

    canSelect() {
            return !this.refreshing || !this.enoughPoints;
    }

    sunsRepositoryChanged(evt) {
        if(evt.suns>=this.points) {
                this.enoughPoints = true;
        } else {
            this.enoughPoints = false;
        }
    }

    plantPlaced(evt) {
        if(evt.plant.name === this.plantToSeed.name) {
            this.refreshing = true;     
            this.selected=false;
            cc.find("/Canvas").emit("slected-plant",{plant:null});
            this.sunRepository.emit("update-suns",{points:-this.points});
        }
    }

    updateAppearance(dt) {
        if(this.refreshing) {
            this.veil.height = this.veil.height + this.refreshRate*dt;
            if(this.veil.height>=this.tileHeight) {
                this.refreshing = false;
                this.veil.height = 0;
            }                
        }
        if(!this.canSelect() && this.veil.opacity!=122) {
            this.veil.height = this.node.height;
            this.veil.color = new cc.Color(0,0,0);
            this.veil.opacity = 122;
        }
        if(this.selected && !this.selectedNode.active) {
            this.selectedNode.active = true;
        }

        if(!this.selected && this.selectedNode.active) {
            this.selectedNode.active = false;
        }
    }
    
    gameCallout:cc.Node= null;
    start () {
        this.tileHeight = this.node.height;
        this.sunRepository = cc.find("/Canvas/HUD/SunIndicator");
        this.sunRepositoryController = this.sunRepository.getComponent(StarRepositoryController);
        this.node.on(cc.Node.EventType.TOUCH_END, this.handleClick, this);
        this.sunRepository.on("suns-changed", this.sunsRepositoryChanged, this);
        cc.find("/Canvas").on("plant-placed", this.plantPlaced, this);
        this.veil = this.node.getChildByName("veil");
        this.selectedNode = this.node.getChildByName("sletedNode");
        this.selectedNode.active = false;
        this.veil.height = 0;
        this.gameCallout = cc.find("/Canvas/HUD/GameCallout");
    }

    erazeGameCallout(){
        this.gameCallout.getComponent(cc.Label).string= "";
    }

    handleClick(evt) {
        if(this.sunRepositoryController.suns<=0 ) {
            this.gameCallout.getComponent(cc.Label).string= "You dont have enough suns, click on the suns first to plant";
            this.sunRepositoryController.node.runAction(cc.sequence([cc.scaleTo(.2,1.1),cc.scaleTo(.2,1), cc.delayTime(2),cc.callFunc(this.erazeGameCallout, this)]));
                return;
        } if(this.sunRepositoryController.suns<this.points ) {
            this.gameCallout.getComponent(cc.Label).string= "You need atleast "+this.points+" suns to plant";
            this.sunRepositoryController.node.runAction(cc.sequence([cc.scaleTo(.2,1.1),cc.scaleTo(.2,1), cc.delayTime(2), cc.callFunc(this.erazeGameCallout, this)]));
                return;
        }

        if(!this.canSelect()) {
                this.node.runAction(cc.sequence([cc.scaleTo(.2,1.1),cc.scaleTo(.2,1)]));
                return;
        }
        this.selected = true;
        cc.find("/Canvas").emit("slected-plant",{plant:this.plantToSeed});
    }

    update (dt) {
       this.updateAppearance(dt);
    }
}
