// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class Movement extends cc.Component {


    owner:EggMan = null;

    @property
    rotationSpeed: number = 70;

    @property
    initialSpeed: cc.Vec2 = new cc.Vec2(0,0);

    @property
    maxSpeed: cc.Vec2 = new cc.Vec2(0,0);;

    @property
    accelaration: cc.Vec2 = new cc.Vec2(10,0);


    lastVelocity: cc.Vec2= new cc.Vec2(0,0);;


    onLoad () {
        this.node.rotation=70;
        this.lastVelocity = this.initialSpeed;
    }

    setOwner (firer:EggMan) {
        this.owner = firer;
    }


    getPosition(dt): cc.Vec2 {

        let dv:cc.Vec2 = this.accelaration.mul(dt);
        this.lastVelocity = this.lastVelocity.add(dv);
        let position:cc.Vec2= this.node.getPosition();
        let finalPosition = position.add(this.lastVelocity.mul(dt));
        console.log("final position of the node is ", finalPosition);
        return finalPosition;
    }

    handleOutOfBounds() {
        if(this.node.x > cc.director.getWinSizeInPixels().width|| 
        this.node.y > cc.director.getWinSizeInPixels().height||
        this.node.x <0||
        this.node.y <0
        ) {
            console.log("Destroying the node as it has gone out of bounds!");
            this.destroySelf();
        }

        if (this.owner && this.owner.getGroundPosition().y > this.node.y-this.node.height*this.node.scale) {
            console.log("Destroying the node as it has gone below the ground!");
            this.destroySelf();
        }

    }

    destroySelf() {
        this.node.destroy();
    }

    update (dt) {   
        this.handleOutOfBounds();
        this.rotate(dt);
        this.node.x = this.getPosition(dt).x;
        this.node.y = this.getPosition(dt).y;

    }

    rotate(dt) {
        this.node.rotation = this.node.rotation - this.rotationSpeed*dt;
        console.log("Totaion is ", this.node.rotation);
    }
}
