const {ccclass, property} = cc._decorator;

@ccclass
export default class SpeechMovement extends cc.Component {

    @property
    time: number = 2;


    setText(text:string) {
        let label:cc.Label = this.node.getComponent(cc.Label);
        label.string = text;
    }


    update (dt) {
        this.node.y += 20*dt;
        this.node.opacity = this.node.opacity - 20*dt;

        if(this.node.opacity <=0) {
            this.node.destroy();
        }
    }
}
