import DebuggableNode from "../util/DebuggableNode";
import GameProgress from "../util/Pojo/GameProgress";
import World from "../util/Pojo/World";
import Level from "../util/Pojo/Level";
import LevelSelectorButton from "./LevelSelectorButton";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;


@ccclass
export default class LevelLoader extends cc.Component {

    @property(cc.Prefab)
    levelButton:cc.Prefab = null

    @property(cc.Prefab)
    lockedButton:cc.Prefab = null

    @property
    world:number = 1

    @property
    numberOfLevels:number = 3

    @property
    rows:number = 2

    @property
    cols: number = 3

    @property
    userAt: number = 1

    @property(cc.Node)
    container:cc.Node = null;


    gameState:GameProgress = null;

    onLoad () {
        this.node.on("loadedLevels", this.layLevelSelectorTiles, this);

    }

   

    getLevelsFromWorld(evt): Array<Level> {
        let levels:Array<Level> = [];
        evt.game.forEach((eachWorld:World)=>{
            if(eachWorld.index === this.world){
                levels = eachWorld.levels;
            }
        });

        return levels;
    }

    layLevelSelectorTiles(evt){
        

        let levels: Array<Level> = this.getLevelsFromWorld(evt);

        let index = 1;
        for(let row = 1; row <= this.rows; row++) {
            for(let col=1; col<=this.cols;col++) {
                if(index<=levels.length) {
                    let level:Level = levels[index-1];

                    if(level.canPlay) {
                        let node = new DebuggableNode();
                        node.width = this.container.width/this.cols;
                        node.height = this.container.height/this.rows;
                        let levelButton:cc.Node = cc.instantiate(this.levelButton);
                        levelButton.getChildByName("level").getComponent(cc.Label).string = ""+level.level;
                        let levelSectorButton:LevelSelectorButton = levelButton.getComponent(LevelSelectorButton);
                        levelSectorButton.sceneName = level.sceneName;
                        this.container.addChild(node);
                        node.addChild(levelButton);
                        levelButton.setPosition(new cc.Vec2(0,0));
                        node.setPosition(new cc.Vec2((col-1)*node.width+node.width/2,-1*((row-1)*node.height+node.height/2))); 
                    }else {
                        let node = new cc.Node();
                        node.width = this.container.width/this.cols;
                        node.height = this.container.height/this.rows;
                        node.setAnchorPoint(new cc.Vec2(0,0));
                        let levelButton:cc.Node = cc.instantiate(this.lockedButton);
                        node.addChild(levelButton);
                        levelButton.getChildByName("level").getComponent(cc.Label).string = ""+index;
                        levelButton.setPosition(new cc.Vec2(0,0));
                        this.container.addChild(node);
                        node.setPosition(new cc.Vec2((col-1)*node.width+node.width/2,-1*((row-1)*node.height+node.height/2))); 
                    }
                    index++;
                }
            }
        }
    }

    // update (dt) {}
}
