import PlantsRepository from "./PlantsRepository";
import GardenPlacement from "./GardenPlacement";
import KejaruController from "../kejru/KejaruController";
import NetaJi from "./NetaJi";
import MovementController from "../kejru/MovementController";
import KejaruSideController from "../../gameObjects/kejaruNormal/KejaruSideController";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GameController extends cc.Component {

    @property(cc.Prefab)
    kejaru:cc.Prefab = null
   
    netas:Array<cc.Node> = []

    @property
    waveOneNetas:number=1

    @property
    waveTwoNetas:number =10


    @property
    waveFinalNetas:number =20

    
    gardenPlacement:GardenPlacement
    
    plantSelected(evt) {
        
    }

    canPlace = [0,1,2,3,4];
    handleNetaJiDead(evt) {
        let foundIndex = -1;
        this.netas.forEach(function(neta, index){
            if(neta.uuid == evt.uuid) {
                foundIndex = index;
                
            }
        });
        if(foundIndex !=-1){
            this.netas.splice(foundIndex,1);
        }
        if(this.netas.length == 0){
            this.sendNextNetaJi();
        }
    }

    waveNumber:number=1;

    sendNextNetaJi() {
        if(this.canPlace.length==0) {
            this.canPlace = [0,1,2,3,4]; 
            this.waveNumber +=1;
        }
        let randomRow = Math.floor(this.canPlace.length*Math.random());
        let position = null;
        if(randomRow != 0) {
            position = this.gardenPlacement.getTileFromIndex(randomRow*9-1).getBottomMostMitPoint();
        }else {
            position = this.gardenPlacement.getTileFromIndex(8).getBottomMostMitPoint();
        }
        this.canPlace.splice(randomRow,1);
        this.callout.getComponent(cc.Label).string = "";
        
        //console.log(index);
        let netaji = cc.instantiate(this.kejaru);
        let movementController: KejaruSideController = netaji.getComponent("KejaruSideController");
        movementController.walkingSpeed += movementController.walkingSpeed*(2*Math.random());
        let netaJi: NetaJi = netaji.getComponent(NetaJi);
        netaJi.row = this.gardenPlacement.getTileFromIndex(randomRow*9-1).row;
        netaJi.column = 9;
        this.netas.push(netaji);
        let alreadyPlaced = 0;
        cc.director.getScene().addChild(netaji);
        netaji.setPosition(position);
    }

    startNetaJiWaves() {
    
    }

    handleNetaJiWins(evt) {
        this.callout.getComponent(cc.Label).string = "YOU LOOSE Neta Ji wins";
    }

    callout:cc.Node=null;
    start () {
        cc.find("/Canvas").on("plant-slected", this.plantSelected, this);
        cc.find("/Canvas").on("netaJiDied", this.handleNetaJiDead, this);
        cc.find("/Canvas").on("NetaJiWins", this.handleNetaJiWins, this);
        this.gardenPlacement = cc.find("/Canvas/bg/gardenMesh").getComponent("GardenPlacement");
        this.callout = cc.find("/Canvas/HUD/GameCallout");
        let totalNetajis = this.waveOneNetas + this.waveTwoNetas + this.waveFinalNetas;
        this.callout.getComponent(cc.Label).string = "Collect Suns \n  and plant trees to \n defend your garden";
        this.node.runAction(cc.sequence([cc.delayTime(5), cc.callFunc(this.sendNextNetaJi, this)]));
        
    }
}
