const {ccclass, property} = cc._decorator;

@ccclass
export default class WaveController extends cc.Component {

    @property
    animationTiming:number = 10


    @property
    damage:number = 100
    

    scaleAnimation:cc.ActionInterval = null;

    launchWave() {
        this.scaleAnimation = cc.scaleTo(this.animationTiming,5);
        let animation = cc.sequence([this.scaleAnimation , cc.callFunc(this.onAnimationDone, this)]);
        this.node.runAction(animation);
    }


    onLoad () {
        this.launchWave();
        cc.director.getCollisionManager().enabled = true;
    }

    onCollisionEnter(other:cc.Collider, self:cc.Collider) {
        if(other.node.group === "netaJiCanDestroy") {
            other.node.emit("takeHitFromNetaJi", {points: this.damage,
                damager: this.node.group})
            this.node.destroy();
        }
    }

    onAnimationDone() {
        this.node.destroy();
    }

    start () {

    }

    // update (dt) {}
}
