const {ccclass, property} = cc._decorator;

@ccclass
export default class Plant extends cc.Component {

    row:number = 0
    column:number = 0
    index: number = 0
    worldPosition: cc.Vec2 = null

    proximityTagForNetaJi:number = 10;
    healthIndicator: cc.Node = null;

    onLoad () {
        this.healthIndicator = this.node.getChildByName("healthIndicator");
        cc.director.getCollisionManager().enabled = true;
        // cc.director.getCollisionManager().enabledDebugDraw = true;
        // cc.director.getCollisionManager().enabledDrawBoundingBox = true;
    }

    @property
    health:number = 200
    currentTime: number = 0;

    reactToNetajiProximity(netaJiNear:boolean){

    }

    onCollisionEnter(other:cc.Collider, self:cc.Collider) {

        if(other.node.group === "netaJi" && other.tag ==0 && self.tag === this.proximityTagForNetaJi) {
            let netaJi: NetaJi = other.node.getComponent(NetaJi);
            if(netaJi.row === this.row) {
                this.reactToNetajiProximity(true);
            } 
            return;
        }

        if(other.node.group === "netaJi" && other.tag === 0) {
            let netaJi: NetaJi = other.node.getComponent(NetaJi);
            if(netaJi.row === this.row) {
                other.node.emit("eatPlant", {plant: this});
            }        
        }
    }

    onCollisionStay(other:cc.Collider, self:cc.Collider) {
        if(other.node.group === "netaJi") {
            let netaJi: NetaJi = other.node.getComponent(NetaJi);
            if(netaJi.row === this.row) {
                this.reactToNetajiProximity(true);
            } 
            
        }
    }

    onCollisionExit(other:cc.Collider, self:cc.Collider) {

        if(other.node.group === "netaJi") {
            let netaJi: NetaJi = other.node.getComponent(NetaJi);
            if(netaJi.row === this.row) {
                this.reactToNetajiProximity(false);
            } 
        }
    }


    // update (dt) {}
}
