const {ccclass, property} = cc._decorator;

@ccclass
export default class LawnMover extends MovementController {


    audio:cc.AudioSource = null


    originalSpeed: number = 0;

    start () {

    }

    onCollisionEnter(other:cc.Collider, self:cc.Collider) {
        if(other.node.group === "netaJi" && other.tag === 128) {
            other.node.emit("crushedByLawnMover");
            this.run();
        }
    }

    run() {
        this.walkingSpeed = this.originalSpeed;
        this.audio.play();
    }


    onLoad() {
        this.audio = this.node.getComponent(cc.AudioSource);
        cc.director.getCollisionManager().enabled = true;
        //   cc.director.getCollisionManager().enabledDebugDraw = true;
        // cc.director.getCollisionManager().enabledDrawBoundingBox = true;
        this.originalSpeed = this.walkingSpeed;
        this.walkingSpeed = 0;
    }

    onCollisionStay (other:any, self:any) {
        //console.log('on collision stay');
    }
    
    onCollisionExit (other:any, self:any) {
       // console.log('on collision exit');
    }
}
