const {ccclass, property} = cc._decorator;

@ccclass
export default class MainCamera extends cc.Component {

    @property(cc.Node)
    HUD:cc.Node = null;


    rightMovement:number = 0

    @property
    margin:number =10



    
    camera:cc.Node = null

    moveCameraToExtremeRight() {
        return cc.moveTo(1, new cc.Vec2(this.rightMovement,0));
    }

    moveBackToOriginalPlace() {
        return cc.moveTo(1, new cc.Vec2(0,0));
    }

    setToViewGarden() {
        this.HUD.active = true;
       let cameraFocusHeight = this.HUD.height;
       let deviceHeight = cc.director.getWinSizeInPixels().height;
        this.camera.setPosition((this.HUD.getPosition()));
       this.camera.getComponent(cc.Camera).zoomRatio = deviceHeight/cameraFocusHeight;
       //this.bgSprite.setScale(new cc.Vec2(this.garden.width/525, appliedScale));
       //cc.find("/Canvas/bg/Garden").setScale(new cc.Vec2(this.garden.width/525, appliedScale));

    }

    animationSequence:any = null;

    onLoad() {
        let width = cc.director.getWinSizeInPixels().width;
        // background Image 1480
        this.camera = cc.find("/Canvas/Main Camera");
        this.rightMovement = (cc.find("/Canvas/bg").width - width)/2;
        this.camera.setPosition(0,0);
        this.animationSequence = cc.sequence([this.moveCameraToExtremeRight(),this.moveBackToOriginalPlace(), cc.callFunc(this.setToViewGarden, this)]);
    }

    start () {
        this.camera.runAction( this.animationSequence);
    }

    // update (dt) {}
}
