const {ccclass} = cc._decorator;

@ccclass
export default class AnimationsUtil {

    upAndDownMovement(node:cc.Node, totalAnimationTime = 2) {
        let moveUpAnimation = cc.moveBy(totalAnimationTime/2, cc.v2(0, 10));
        let moveDownAnimation = cc.moveBy(totalAnimationTime/2, cc.v2(0, -10));
        let upAndDownAnimationSequence = cc.sequence([moveUpAnimation, moveDownAnimation]);
        let repeatSequence = cc.repeatForever(upAndDownAnimationSequence);
        node.runAction(repeatSequence);
    }


    openAndCloseAnimation(node:cc.Node, totalAnimationTime = 2) {
        let open = cc.scaleTo(totalAnimationTime/2, .2, .2);
        let close = cc.scaleTo(totalAnimationTime/2, .2, .01);
        let animationSequence  = cc.sequence([open, close]);
        let openAndCloseAction = cc.repeatForever(animationSequence);
        node.runAction(openAndCloseAction);
    }
}
