const {ccclass, property} = cc._decorator;

@ccclass
export default class SunFlowerController extends Plant {

    @property(cc.Prefab)
    sun:cc.Prefab = null

    @property
    productionRate:number = 3

    currentTime:number = 0

    start () {
        this.node.on("takeHit", this.takeHit, this);
    }

    takeHit(evt) {
        this.health -= evt.points;
        this.healthIndicator.emit("takeHit", evt);
        if(this.health<0) {
            this.node.destroy();
            evt.node.emit("doneEating");
        }
    }

    update(dt) {
        this.currentTime += dt;

        if(this.currentTime> this.productionRate) {
            this.currentTime = 0;
            let sun = cc.instantiate(this.sun);
            cc.director.getScene().addChild(sun);
            sun.setPosition(this.node.convertToWorldSpaceAR(new cc.Vec2()));
        }
    }
}
