import Plant from "./Plant";
import TileClickHandler from "../groundTile/TileClickHandler";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GardenPlacement extends cc.Component {

    selectedPlant:cc.Prefab = null;
    plantSelected(evt) {
        this.selectedPlant = evt.plant;
    }
    rows:number = 5;
    columns:number = 9;
    plants={}
    tiles={}
    plantScale:cc.Vec2 = null

    getTileFromIndex(index) {
        let tile:TileClickHandler = this.tiles[index];
        return tile;
    }

    getPositionFromIndex(index) {
        let tile:TileClickHandler = this.tiles[index];
        return tile.getWorldPosition();
    }
    handlePlantDied(plant:cc.Node) {
        for(let i=0;i<this.rows*this.columns;i++) {
            let currentPlant:cc.Node = this.plants[i];
            if(currentPlant){
                if(currentPlant.uuid === plant.uuid) {
                    delete this.plants[i];
                }
            }
        }
    }

    tilesHaveBeenLayed(evt) {
        this.tiles = evt.tiles;
    }

    onLoad() {
        cc.find("/Canvas").on("slected-plant", this.plantSelected, this);

        cc.find("/Canvas").on("plant-died", this.handlePlantDied, this);

        cc.find("/Canvas").on("tileClicked", this.handleTileClicked, this);
        this.node.on("tilesLayed", this.tilesHaveBeenLayed, this);
    }
    start () {
    }
    plantPresentAtIndex(index) {
        if(this.plants[index]) {
            return true;
        }
        return false;
    }

    handleTileClicked(evt) {
        let index = evt.index;
        if(this.selectedPlant != null &&  !this.plantPresentAtIndex(index)){
            let plant = cc.instantiate(this.selectedPlant);
            cc.director.getScene().addChild(plant);
            if(this.plantScale == null) {
                if(plant.width > evt.width || plant.height> evt.height) {
                    this.plantScale = new cc.Vec2(plant.getScale(new cc.Vec2()).x* evt.width/plant.width, plant.getScale(new cc.Vec2()).y*evt.height/plant.height);
                }else {
                    this.plantScale = plant.getScale(new cc.Vec2());
                }    
            }
            plant.setScale(this.plantScale );
            let plantComponent: Plant = plant.getComponent(Plant);
            plantComponent.row=  Math.floor(index/this.columns);
            plantComponent.column=  index%this.columns;
            plantComponent.worldPosition = evt.pos;
            plantComponent.index = index;
            plant.setPosition(evt.lowerCenter);
            this.plants[index] = plant;
            cc.find("/Canvas").emit("plant-placed",{
                plant: plant,
                row: index/this.columns,
                column:index%this.columns
            });

        }

       
    }

}
