const {ccclass, property} = cc._decorator;

@ccclass
export default class EggGenerator extends cc.Component {

    @property(cc.Prefab)
    egg: cc.Node = null;

    eggGenerationPoint: cc.Vec2;


    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        let eggOrigin:cc.Node = this.node.getChildByName("eggOrigin");
        this.eggGenerationPoint = eggOrigin.getPosition().add(this.node.getPosition());
    }

    start () {
        var scene = cc.director.getScene();
        var newEgg = cc.instantiate(this.egg);
        newEgg.setScale( this.node.getScale(new cc.Vec2()).mul(.3));
        newEgg.setPosition(this.eggGenerationPoint);
	    newEgg.parent = scene;
    }



    // update (dt) {}
}
